/* Responsive Calendar for Foundation */
.calendar {
  margin-left: 0;
  margin-bottom: 1.25rem;
  padding-bottom: 1px;
  border-bottom: 2px solid #2ba6cb; }

.calendar * {
  list-style: none;
  line-height: 1; }

.calendar .title {
  text-align: center; }

.calendar .week {
  padding: 0px;
  margin: 0px; }

.calendar .week:before, .calendar .week:after {
  content: " ";
  display: table; }

.calendar .week:after {
  clear: both; }

.calendar .day,
.calendar .days {
  position: relative;
  padding: 0.3rem 0.39375rem;
  width: 100%;
  min-height: 6rem;
  float: left; }

.calendar .day-header {
  background-color: #2ba6cb; }

.calendar .day-header .day,
.calendar .day-header .days {
  background-color: #2ba6cb;
  color: white;
  text-align: center;
  min-height: 0.5rem;
  display: none; }

.day:hover, .days:hover {
  background: #efefef; }

.day.previous-month,
.days.previous-month,
.day.next-month,
.days.next-month {
  background: #e6e6e6;
  color: #222222; }

.day.today, .days.today {
  background: #76df9d; }

.day.today:hover, .days.today:hover {
  background: #65ce8c; }

